body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff6ef; /* A nice peach color */
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  display: flex;
  max-width: 700px;
  margin: 0 auto;
  padding: 20px;
}

div {
  width: 100%;
}

.editor {
  position: relative;
}

.editor .text-input {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  pointer-events: none;
}

.text {
  background: white;
  border: 2px solid transparent;
  border-radius: 4px;
  color: black;
  width: 100%;
  min-height: 200px;
  box-shadow: 0 1px 5px rgba(0,0,0,0.3);
  padding: 0.5em;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;

  transition: border .2s ease-in;
}

.text.writing-mode {
  color: rgba(0,0,0,0.03);
  cursor: default;
}

.text.writing-mode::selection {
  background-color: transparent;
}

.text-input:focus ~ .text {
  border: 2px solid rebeccapurple;
}

@keyframes blink {
  0% { opacity: 0; }
  35% { opacity: 0; }
  40% { opacity: 1; }
}

.text-input:focus ~ .text .text-wrapper::after {
  animation: 1.1s ease-in blink infinite;
  content: '';
  display: inline-block;
  width: 1px;
  height: 1.2em;
  background-color: #111;
}

.writing-prompt {
  width: 100%;
  font-size: 1.25em;
  font-family: -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  border: 0;
  border-bottom: 1px solid black;
  margin-bottom: 1em;
  padding: 0.25em;
}

.writing-prompt:focus {
  border-bottom-color: rebeccapurple;
  outline: 0;
}
